	module.exports = {"data":{"markdownRemark":{"html":"<h2>Math Ceil</h2>\n<p>The <code>Math.ceil()</code> is a method of the Math standard object that rounds a given number upwards to the next integer. Take note that for negative numbers this means that the number will get rounded “towards 0” instead of the number of greater absolute value (see examples).</p>\n<h3>Examples</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code>Math<span class=\"token punctuation\">.</span><span class=\"token function\">ceil</span><span class=\"token punctuation\">(</span><span class=\"token number\">0.1</span><span class=\"token punctuation\">)</span>  <span class=\"token comment\" spellcheck=\"true\">//  1</span>\nMath<span class=\"token punctuation\">.</span><span class=\"token function\">ceil</span><span class=\"token punctuation\">(</span><span class=\"token number\">1.3</span><span class=\"token punctuation\">)</span>  <span class=\"token comment\" spellcheck=\"true\">//  2</span>\nMath<span class=\"token punctuation\">.</span><span class=\"token function\">ceil</span><span class=\"token punctuation\">(</span><span class=\"token operator\">-</span><span class=\"token number\">0.9</span><span class=\"token punctuation\">)</span> <span class=\"token comment\" spellcheck=\"true\">// -0</span>\nMath<span class=\"token punctuation\">.</span><span class=\"token function\">ceil</span><span class=\"token punctuation\">(</span><span class=\"token operator\">-</span><span class=\"token number\">1.5</span><span class=\"token punctuation\">)</span> <span class=\"token comment\" spellcheck=\"true\">// -1</span>\n</code></pre>\n      </div>\n<h3>More Information:</h3>\n<ul>\n<li><a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Math/ceil\">MDN</a></li>\n<li><a href=\"https://www.w3schools.com/jsref/jsref_ceil.asp\">w3schools</a></li>\n<li><a href=\"https://en.wikipedia.org/wiki/Floor_and_ceiling_functions\">Wikipedia</a></li>\n</ul>","fields":{"slug":"/javascript/standard-objects/math/math-ceil/"},"frontmatter":{"title":"Math Ceil"}}},"pathContext":{"slug":"/javascript/standard-objects/math/math-ceil/"}}

